.container {
  max-width: 160rem;
  margin: 0 auto;
  padding: 12.8rem;
}

@media (max-width: 1500px) {
  .container {
    padding: 9.6rem;
  }
}

@media (max-width: 1000px) {
  .container {
    padding: 6.4rem;
  }
}

@media (max-width: 800px) {
  .container {
    padding: 6.4rem 4.8rem;
  }
}

@media (max-width: 550px) {
  .container {
    padding: 6.4rem 3.2rem;
  }
}

@media (max-width: 450px) {
  .container {
    padding: 6.4rem 2.4rem;
  }
}
